.aboutbox>p {
  font-family: 'Fredericka the Great', cursive;
  border-bottom: 1px solid gold;
  font-size:1.5em;
  text-align: justify;
  display: flow-root;
}
.bio .date{
float:left;
}
.skills .date{
  float:right;
}
.aboutbox>p:not(.bio){
  padding-top: 50px;
}
.aboutbox>p{
  margin: 0 50px 0 50px;
  padding-bottom: 50px;
  /* border: 1px solid; */
}
.skills>p span{
  color:gold;
}
.date p {
  font-family: "Lobster", cursive;
  font-size: 3em;
  line-height: 90px;
}
.date {
  border: 4px gold solid;
  shape-outside: circle(95px);
  width: 90px;
  height: 90px;
  border-radius: 90px;
  text-align:center;
  display: table-cell;
  vertical-align:middle;
  margin: 50px;
  /* background-color: gold; */
  /* overflow: hidden; */
}

