.navTop {
    margin: 50px;
    display: flex;
    justify-content: flex-end;
    text-align: center;
  }
  .back-arrow{
    position: relative;
  }
  .back-arrow::before{
    position: absolute;
    content:"<";
    color: gold;
    font-family: "Amatic SC", cursive;
    font-size: 12em;
    right: 10px;
    top: -65px;
  }
  .navTop a {
    padding: 0 3% 0 3%;
  }
  .right img{
    height:3em;
    width: 3em;
    margin: 5%;
  }
  .back-arrow:hover:before{
    color: deeppink;
  }
  .right img:hover {
    filter: invert(39%) sepia(90%) saturate(7374%) hue-rotate(317deg) brightness(105%) contrast(101%);
  }