@media screen and (max-width: 768px) {
  body {
    overscroll-behavior: auto;
  }
  .responsive {
    height:100%;
    width: 100%;
    display: inline-grid;
    grid-template-columns: auto auto;
    grid-template-rows:auto auto;
  align-items: center;
  }
  .resNav,.resWelcome,.iconsnav{
    border: 1px solid gold;
    padding: 35px;
    height: 100%;
  }
  h1, .resNav {
    font-size: 3em;
  }
  .resNav{
      grid-area: 1/1/1/1;
      align-self:center;
    }
    .resNav li{
      padding: 20% 0 20% 0;
    }
    .resNav i {
      color: gold;
      text-shadow: 2px 2px 2px grey;
    }
    .resWelcome {
      grid-area: 1/2/1/2;
      font-size: 8vh;
      text-align: end;
      text-shadow: 1px 1px black;
      font-family: "Amatic SC", cursive;
    }
    .iconsnav{
      grid-area: 2/1/2/3;
      line-height: 3em;

    }
    .about,
    .projects {
      padding: 0 0 30px 0;
    }
    .projects li:nth-child(6) {
      font-size: 2em;
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
      padding: 30px;
    }
    .projects li a {
      padding: 15px;
      border: 2px solid white;
      border-radius: 50%;
      width: 80px;
      height: 80px;
      display: grid;
      place-content: center;
    }
    ul span {
      display: none;
    }
  }